.loader {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 36px;
  margin-bottom: 30px;

  @media (max-width: 809px) {
    grid-template-columns: repeat(1, 1fr);
  }
  div {
    background-color: white;
    border-radius: 13px;
  }
}
.loader-header {
  max-width: 1200px;

  background-color: white;
  border-radius: 13px;
  margin: 25px auto;
}
