.about {
  margin: 100px 0 40px 0;
  width: 73%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px 40px 20px;
  @media (max-width: 1000px) {
    margin: 80px 0 40px 0;
  }
  @media (max-width: 861px) {
    margin: 80px 0 40px 0;
  }
  h1 {
    font-size: 56px;
    font-family: "Heading 1";
    text-align: center;
    span {
      color: #2c60e7;
    }
    @media (max-width: 1260px) {
      font-size: 40px;
    }
    @media (max-width: 700px) {
      font-size: 30px;
    }
    @media (max-width: 434px) {
      font-size: 20px;
    }
  }

  &__launched {
    max-width: 725px;
    text-align: center;
    color: #000038cc;
    font-size: 16px;
    padding-bottom: 64px;
    @media (max-width: 1260px) {
      font-size: 15px;
      max-width: 525px;
      padding-bottom: 44px;
    }
    @media (max-width: 700px) {
      font-size: 13px;
      max-width: 425px;
      padding-bottom: 34px;
    }
    @media (max-width: 439px) {
      font-size: 10px;
      max-width: 325px;
      padding-bottom: 24px;
    }
  }

  &__timeline {
    display: flex;
    border-top: 4px solid #bacaf4;
    padding-bottom: 200px;
    align-self: flex-end;
    width: 80%;
    @media (max-width: 1260px) {
      display: none;
    }
    div {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      width: 380px;
      &:nth-of-type(3) {
        width: 170px;
      }
      img {
        position: absolute;
        left: 0;
        top: -25px;
      }
    }
    &-card {
      display: flex;
      flex-direction: column;
      background-color: white;
      box-shadow: 0px 8px 32px rgba(0, 0, 46, 0.16), inset 0px 2px 0px #2c60e7;
      border-radius: 15px;
      position: absolute;
      top: 90px;
      left: -42%;
      max-width: 273px;
      padding: 16px;
    }

    ul {
      width: 100%;
      li {
        margin-left: 20px;
        &::before {
          content: "\2022";
          color: #2c60e7;
          font-weight: bold;
          display: inline-block;
          width: 1em;
          margin-left: -1em;
        }
      }
    }
  }
  &__staff {
    h1 {
      text-align: center;
      padding-bottom: 40px;
      @media (max-width: 1260px) {
        font-size: 40px;
        padding-bottom: 30px;
      }
      @media (max-width: 700px) {
        font-size: 30px;
        padding-bottom: 20px;
      }
      @media (max-width: 434px) {
        font-size: 20px;
        padding-bottom: 10px;
      }
    }
    h2 {
      font-family: "Body L Medium";
      text-align: center;
      padding-bottom: 24px;
      font-size: 32px;
      @media (max-width: 1260px) {
        font-size: 25px;
        padding-bottom: 20px;
      }
      @media (max-width: 700px) {
        font-size: 20px;
        padding-bottom: 15px;
      }
      @media (max-width: 434px) {
        font-size: 15px;
        padding-bottom: 10px;
      }
    }
    &-container {
      display: flex;
      justify-content: center;
      gap: 35px;
      padding-bottom: 63px;
      flex-wrap: wrap;
    }
    &-founders {
      position: relative;

      &-pic {
        max-width: 260px;
        max-height: 260px;
        border-radius: 13px;
        filter: grayscale(100%);
        overflow: hidden;

        @media (max-width: 617px) {
          max-width: 220px;
          max-height: 220px;
        }
        @media (max-width: 517px) {
          max-width: 150px;
          max-height: 150px;
        }
        @media (max-width: 478px) {
          max-width: 260px;
          max-height: 260px;
        }
        img {
          max-width: 100%;
          -webkit-transition: all 0.3s ease-in-out;
          &:hover {
            transform: scale(1.05);
          }
        }
        .hidden {
          opacity: 1;
        }
      }
      .role {
        position: absolute;
        bottom: 54px;
        left: 8px;
        background-color: #2c60e7;
        border-radius: 6px;
        color: white;
        font-size: 12px;
        font-family: "Body M";
        padding: 6px 14px;
        @media (max-width: 575px) {
          font-size: 12px;
          bottom: 45px;
        }
      }
      .name {
        display: flex;
        justify-content: space-between;
        margin-top: 9px;
        cursor: pointer;
        padding-right: 19px;
        -webkit-transition: all 0.3s ease-in-out;
        a {
          -webkit-transition: all 0.3s ease-in-out;
          &:hover {
            color: #4074fb;
          }
        }
        &:hover {
          color: #4074fb;
          stroke: #4074fb;
          padding-right: 10px;
        }
      }
      p {
        font-size: 18px;
        font-family: "Regular semibold";
        padding: 5px 14px;
        @media (max-width: 575px) {
          font-size: 12px;
        }
        @media (max-width: 379px) {
          font-size: 15px;
        }
      }
    }
  }
  .blur-image-placeholder {
    position: absolute;
    object-fit: cover;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #8d8c8cbe;

    z-index: 1;
    opacity: 1;
    transition: opacity 0.3s ease;
  }
  .blur {
    filter: blur(3px) grayscale(100%);
  }
}
